import './app.styl'

class App extends Component {
  componentDidMount() {}
  componentDidShow() {}
  componentDidHide() {}
  componentDidCatchError() {}

  //  App  render() 
  // 
  render() {
    return <Provider store={store}>{this.props.children}</Provider>
  }
}

export default App
